export default {
	props: {
		tabBars: Array,
		tabIndex: Number,
		scrollIndex: String
	},
	data() {
		return {
			scrollInto: this.scrollIndex
		};
	},
	methods: {
		changeTabIndex(index) {
			this.$emit('changeIndex', index);
		}
	}
};
